.menu {
  position: absolute;
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  z-index: 9999;
}
.menu_item {
  margin-top: 10px;
  min-width: 200px;
  font-size: 12px;
  line-height: 20px;
  color: #333333;
  cursor: pointer;
}
.menu_item:first-of-type {
  margin-top: 0;
}
.menu_item:hover {
  background: #f0f0f0;
}
.menu_item span,
.menu_item .btn-upload {
  display: inline-block;
  padding: 4px 0;
  padding-left: 24px;
  width: 100%;
}
.menu_item .btn-upload {
  margin: 0;
  border: none;
  outline: none;
  background: transparent;
}
.menu_item .btn-upload:hover {
  background: #aaaaaa;
}
.menu_item ::v-deep .el-upload {
  width: 100%;
}
.menu__group {
  padding-top: 6px;
  padding-bottom: 6px;
  border-bottom: 1px solid #eeeeee;
}
.menu__group:last-of-type {
  border-bottom: none;
}
li:hover {
  background-color: #1790ff;
}
li {
  font-size: 15px;
  list-style: none;
}
